import {Agent, EnvState} from "./Agent";
import {PlayerInput} from "./GameRunner";
import {DQNEnv, DQNOpt, DQNSolver} from "reinforce-js";
import Matter from "matter-js";

import config from './MachineLearningAgentConfig';

export class MachineLearningAgent implements Agent {

    // Machine Learning
    private ml_solver : DQNSolver;
    private ml_env : DQNEnv;
    /*
    inputs:
    1. angle of attack of red
    2. distance from red
    3. angle of attack from sun
    4. distance from sun
    5. red is alive
    6. blue is alive
 */
    private readonly ML_NUM_INPUTS = 6;

    /*
    actions:
    1. do nothing
    2. turn left
    3. turn right
    4. fire
     */
    private readonly ML_NUM_ACTIONS = 4;
    private readonly ML_TEMPORAL_WINDOW = 30;

    private tickCount : number = 0;

    constructor(width: number, height: number, isTraining: boolean) {

        // Machine Learning
        this.ml_env = new DQNEnv(width, height, this.ML_NUM_INPUTS, this.ML_NUM_ACTIONS);
        let ml_opts = this.getMlOptions(isTraining);
        this.ml_solver = new DQNSolver(this.ml_env, ml_opts);
        this.ml_solver.fromJSON(config);
        this.tickCount = 0;
    }

    private getMlOptions(isTraining: boolean): DQNOpt {

        var network_size = this.ML_NUM_INPUTS*this.ML_TEMPORAL_WINDOW + this.ML_NUM_ACTIONS*this.ML_TEMPORAL_WINDOW + this.ML_NUM_INPUTS;

        let layer_defs: Array<any> = [];
        layer_defs.push({type:'input', out_sx:1, out_sy:1, out_depth:network_size});
        layer_defs.push({type:'fc', num_neurons: 50, activation:'relu'});
        layer_defs.push({type:'fc', num_neurons: 50, activation:'relu'});
        layer_defs.push({type:'regression', num_classes: this.ML_NUM_ACTIONS}); //  num_neurons

        let opts: DQNOpt = new DQNOpt();
        opts.setTrainingMode(isTraining);
        opts.setNumberOfHiddenUnits([1000]);  // mind the array here, currently only one layer supported! Preparation for DNN in progress...
        opts.setEpsilonDecay(1.0, 0.1, 1e6);
        opts.setEpsilon(0.05);
        opts.setGamma(0.9);
        opts.setAlpha(0.005);
        opts.setLossClipping(true);
        opts.setLossClamp(1.0);
        opts.setRewardClipping(true);
        opts.setRewardClamp(1.0);
        opts.setExperienceSize(1e6);
        opts.setReplayInterval(100);
        opts.setReplaySteps(100);

        return opts;
    }

    decideNextTickInput(envState: EnvState): PlayerInput {
        let playerInput: PlayerInput = {
            rotateLeft: false,
            rotateRight: false,
            accelUp: false,
            accelDown: false,
            fireBullet: false
        };

        /*
        1. angle of attack of red
        2. distance from red
        3. angle of attack from sun
        4. distance from sun
        5. red is alive
        6. blue is alive
        7. red has bullet
        8. blue has bullet
         */

        let sunDifferenceVector = Matter.Vector.sub(envState.player2.position, envState.sun.position);
        let sunDistance = Matter.Vector.magnitude(sunDifferenceVector);
        let sunAngleOfAttack = Matter.Vector.angle(envState.player2.position, envState.sun.position);

        let playerDifferenceVector = Matter.Vector.sub(envState.player1.position, envState.player2.position);
        let playerDistance = Matter.Vector.magnitude(playerDifferenceVector);
        let playerAngleOfAttack = Matter.Vector.angle(envState.player1.position, envState.player2.position);

        let nextState = [
            playerAngleOfAttack,
            playerDistance,
            sunAngleOfAttack,
            sunDistance,
            envState.player1Alive ? 1 : 0,
            envState.player2Alive ? 1 : 0,
            envState.player1Bullets,
            envState.player2Bullets
        ];

        let action = this.ml_solver.decide(nextState);

        if (action === 0) {
            // do nothing!
        } else if (action === 1) {
            playerInput.rotateLeft = true;
        } else if (action === 2) {
            playerInput.rotateRight = true;
        } else if (action === 3) {
            playerInput.fireBullet = true;
        } else if (action === 4) {
            //playerInput.accelUp = true;
        }
        else {
            // do nothing
        }

        return playerInput;
    }

    rewardAfterTick(envState: EnvState): void {
        let playerDifferenceVector = Matter.Vector.sub(envState.player1.position, envState.player2.position);
        let playerDistance = Matter.Vector.magnitude(playerDifferenceVector);

        let sunDifferenceVector = Matter.Vector.sub(envState.player2.position, envState.sun.position);
        let sunDistance = Matter.Vector.magnitude(sunDifferenceVector);
        this.tickCount++;

        //this.ml_solver.learn( Math.log(this.tickCount/3600));

    }

    loadState(state: any): void {
        this.ml_solver.fromJSON(state);
    }

    saveState(): any {
        return this.ml_solver.toJSON();
    }

    rewardGame(didWin: boolean): void {

        if (didWin) {
            this.ml_solver.learn( 1000);
        } else {
            this.ml_solver.learn( -1000);
        }
    }

}