import {GameRunner} from "./GameRunner";
import {Agent} from "./Agent";
import {MachineLearningAgent} from "./MachineLearningAgent";

export interface Score {
    rounds: number,
    winsShip1: number,
    winsShip2: number,
    bulletsFired: number
}

export enum PlayerType {
    Human,
    AILevel1,
    AILevel2
}

export interface GameOptions {
    player1: PlayerType,
    player2: PlayerType,
    width: number,
    height: number,
    showInstructions: boolean,
    trainAgent: boolean
}

export class WebSpaceDuel {

    private readonly score : Score;
    private runner: GameRunner | null;
    private gameOptions: GameOptions | null;
    private mountElement: HTMLDivElement | null;
    private player2Agent: Agent | null;

    constructor() {
        this.score = {
            rounds: 0,
            winsShip1: 0,
            winsShip2: 0,
            bulletsFired: 0
        };

        this.runner = null;
        this.gameOptions = null;
        this.mountElement = null;
        this.player2Agent = null;
    }

    public mountGame(mountElement: HTMLDivElement){
        this.mountElement = mountElement;
    }

    public newGame(options: GameOptions){
        if(this.mountElement){
            this.gameOptions = options;
            if (this.player2Agent == null || !options.trainAgent){
                this.player2Agent = new MachineLearningAgent(options.width, options.height, options.trainAgent);
                //this.player2Agent.loadState();
            }
            this.runner?.destroy();
            this.runner = new GameRunner(
                this.mountElement,
                options.player1 === PlayerType.Human,
                options.player2 === PlayerType.Human,
                this.player2Agent,
                this.onGameEnd.bind(this),
                (options.trainAgent ? 3 : 1),
                options.width,
                options.height
            );
        }
    }

    public onGameEnd(player1Win: boolean) {
        console.log(`Game Ended, player1 won = ${player1Win}`);
        if (!player1Win) {
            console.log(JSON.stringify(this.player2Agent?.saveState()));
        }
        if (this.gameOptions?.trainAgent){
            this.player2Agent?.rewardGame(!player1Win);
            this.newGame(this.gameOptions);
        }
    }

    public pauseGame(){
        if(this.runner){
            this.runner.setRunning(!this.runner.isRunning());
        }
    }

    public isPaused(): boolean{
        console.log("Checking is Paused");
        return this.runner?.isRunning() || false;
    }

    public getScore(): Score {
        return this.score;
    }
}